import{useEffect}from'react';import{EAppEventBusNames,appEventBus}from'@onekeyhq/shared/src/eventBus/appEventBus';import useAppNavigation from"../../../hooks/useAppNavigation";import{useFirmwareUpdateActions}from"../hooks/useFirmwareUpdateActions";export function BootloaderModeUpdateReminder(){var navigation=useAppNavigation();var actions=useFirmwareUpdateActions();useEffect(function(){var fn=function(event){actions.showBootloaderMode({connectId:event.connectId,existsFirmware:event.existsFirmware});};appEventBus.on(EAppEventBusNames.ShowFirmwareUpdateFromBootloaderMode,fn);return function(){appEventBus.off(EAppEventBusNames.ShowFirmwareUpdateFromBootloaderMode,fn);};},[actions,navigation]);return null;}